<script setup>

</script>

<template>

    <div class="container_radar">
        <div class="radar">
            <div class="decoration"></div>
            <div class="enemy_dot"></div>
            <div class="center_dot">
                <div class="detectors animation_on">
                </div>
            </div>
        </div>
    </div>

</template>

<style>

.container_radar{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 300px;
    height: 300px;
    background-color: #949292;
    border: solid 2px #3a3939;
}

.radar{
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 80%;
    height: 80%;
    border-radius: 50%;
    background-color: rgb(8, 8, 179);
    border: solid 2px white ;
    overflow: hidden;
}

.center_dot{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 20%;
    height: 20%;
    border: solid 2px white ;
    border-radius: 50%;
    background-color: rgb(78, 7, 78);
}

.enemy_dot{
    position: absolute;
    top: 50%;
    left: 80%;
    transform: translate(-50%, -50%);
    background-color: rgb(240, 67, 67);
    width: 10px;
    height: 10px;
    border-radius: 50%;
    animation: enemy 3s ease-out infinite;

}

@keyframes enemy {
    0%{
        background-color: transparent;
    }

    50%{
        background-color: red;

    }

    100%{
        background-color: transparent;
    }
}

.decoration{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 40%;
    height: 40%;
    border: solid 2px white;
    box-shadow: 0 0 20px rgb(255, 255, 255);
    border-radius: 50%;
}

.decoration::before{
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 150%;
    height: 150%;
    border: solid 2px white;
    border-radius: 50%;
}

.decoration::after{
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 200%;
    height: 200%;
    border: solid 2px white;
    box-shadow: 0 0 20px rgb(255, 255, 255);
    border-radius: 50%;
}

.detectors{
    position: relative;
    width: 70px;
    height: 100px;
}


.detectors::before{
    content: '';
    top: -55%;
    left: 50%;
    position: absolute;
    transform-origin: left bottom;
    width: 100px; 
    height: 100px; 
    background: #428bca;
    -webkit-transform: rotate(45deg);
    -moz-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    -o-transform: rotate(45deg);
    transform: rotate(45deg);
    background: linear-gradient(to right top , white, rgba(255, 255, 255, 0.493), transparent,transparent);
    animation: animate 6s linear infinite;
    
}


@keyframes animate {
    from{
        transform: rotateZ(0deg);
    }
    to{
        transform: rotateZ(720deg);
    }
}

</style>