    width: 100%;
    padding-top: 20px;
    font-family: 'Poppins', sans-serif;
    font-size: 30px;
    padding-left: 20px;
}

.battle-grid {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    background-color: #eaeaea;
    /* border: solid 2px blue; */
}

.labels-row {
    margin-top: 20px;
    display: flex;
    flex-direction: row;
}

.labels-column {
    display: flex;
    flex-direction: column;
}

.label-cell {
    font-family: 'Poppins', sans-serif;
    width: 62px;
    height: 62px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: bold;
    font-size: 35px;
    background-color: #eaeaea;
}

.row-container {
    display: flex;
    flex-direction: row;
}

.grid-container {
    display: flex;
    flex-direction: column;
}

.row {
    display: flex;
}

.square {
    font-family: 'Poppins', sans-serif;
    width: 60px;
    height: 60px;
    border: 1px solid #001aff;
    box-shadow: 0 0 5px rgb(43, 11, 221);
    cursor: crosshair;
    display: flex;
    justify-content: center;
    align-items: center;
}

.empty {
    background-color: #4a90e2;
}

.ship {
    background-color: white;
}

.hit {
    background-color: #f52b20e8;
    border: ridge .5px;
}

